import React from "react"
import { TabContent, Nav, NavItem, NavLink, Container } from "reactstrap"
import classnames from "classnames"
import UseCasesMicroservices from "./use-cases-microservices"
import UseCasesIoT from "./use-cases-iot"
import UseCasesEventMesh from "./use-cases-event-mesh"
import UseCasesMessaging from "./use-cases-messaging"
import UseCasesCloud from "./use-cases-cloud"

//Import the icons
import iconMicroservices from "../../images/use-cases/icons/icon-microservices.png"
import iconIoT from "../../images/use-cases/icons/icon-iot.png"
import iconEventMesh from "../../images/use-cases/icons/icon-event-mesh.png"
import iconMessaging from "../../images/use-cases/icons/icon-messaging.png"
import iconCloud from "../../images/use-cases/icons/icon-cloud.png"

export default class UseCasesTabs extends React.Component {
  constructor(props) {
    super(props)

    this.toggle = this.toggle.bind(this)
    this.state = {
      activeTab: "1",
    }
  }

  toggle(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab,
      })
    }
  }
  render() {
    return (
      <div>
        <Container fluid={true}>
          <Nav tabs>
            <NavItem>
              <NavLink
                className={classnames({ active: this.state.activeTab === "1" })}
                onClick={() => {
                  this.toggle("1")
                }}
              >
                <img
                  src={iconMicroservices}
                  className="UseCaseTabIcon"
                  alt="Icon - Microservices"
                />
                <span className="UseCaseTabText">Microservices</span>
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                className={classnames({ active: this.state.activeTab === "2" })}
                onClick={() => {
                  this.toggle("2")
                }}
              >
                <img
                  src={iconEventMesh}
                  className="UseCaseTabIcon"
                  alt="Icon - Event Mesh"
                />
                <span className="UseCaseTabText">Event Mesh</span>
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                className={classnames({ active: this.state.activeTab === "3" })}
                onClick={() => {
                  this.toggle("3")
                }}
              >
                <img
                  src={iconIoT}
                  className="UseCaseTabIcon"
                  alt="Icon - IoT"
                />
                <span className="UseCaseTabText">IoT</span>
              </NavLink>
            </NavItem>

            <NavItem>
              <NavLink
                className={classnames({ active: this.state.activeTab === "4" })}
                onClick={() => {
                  this.toggle("4")
                }}
              >
                <img
                  src={iconMessaging}
                  className="UseCaseTabIcon"
                  alt="Icon - Messaging"
                />
                <span className="UseCaseTabText">Messaging</span>
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                className={classnames({ active: this.state.activeTab === "5" })}
                onClick={() => {
                  this.toggle("5")
                }}
              >
                <img
                  src={iconCloud}
                  className="UseCaseTabIcon"
                  alt="Icon - Cloud"
                />
                <span className="UseCaseTabText">Cloud</span>
              </NavLink>
            </NavItem>
          </Nav>
        </Container>
        <TabContent
          activeTab={this.state.activeTab}
          className="gradientDrkGrey"
        >
          <UseCasesMicroservices />
          <UseCasesEventMesh />
          <UseCasesIoT />
          <UseCasesMessaging />
          <UseCasesCloud />
        </TabContent>
      </div>
    )
  }
}
