import React from "react"
export default class LanguageLibrary extends React.Component {
  constructor(props) {
    super(props)
    this.library = props.data
  }

  render() {
    return (
      <div style={{ display: "flex" }}>
        <div className="description" style={{ paddingTop: "15px" }}>
          <div className="name">{this.library.displayName}</div>
        </div>
        <div
          className="description"
          style={{ position: "absolute", left: "350px", paddingTop: "15px" }}
        >
          <div className="name">{this.library.protocol}</div>
        </div>
        <div className="links">
          <div className="get-started">Get Started</div>
        </div>
      </div>
    )
  }
}
